.wrapper {
  overflow: hidden;
}
.h2 {
  font-size: 42px;
  line-height: 130%;
  font-weight: 700;
}
.btn-blue {
  background: #5856D2;
  border-radius: 12px;
  &:hover {
    opacity: 0.8;
  }
  span {
    line-height: 130%;
    font-weight: 600;
    color: #fff;
  }
}
.section-top {
  display: flex;
  align-items: center;
  gap: 15px;
  &__link {
    height: 40px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border: 1px solid #5856D2;
    border-radius: 12px;
    padding: 5px 21px;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    color: #5856D2;
    &:hover {
      background: #5856D2;
      color: #fff;
      i svg path {
        stroke: #fff;
      }
    }
    i {
      width: 7px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
}
.swiper-pagination {
  &-bullet {
    width: 7px;
    height: 7px;
    background: #F2EFF9;
    opacity: 1 !important;
    margin: 0 3.5px !important;
  }
  &-bullet-active {
    background: #3F3F3F;
  }
}
.form {
  &-group {
    position: relative;
    input {
      background: #fff;
      border: 1px solid #fff;
      border-radius: 12px;
      padding: 0 20px 0 45px;
      &::placeholder {
        color: rgba(63, 63, 63, 0.5);
      }
    }
    .input-icon {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translate(0, -50%);
      max-width: 14px;
      pointer-events: none;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .h2 {
    font-size: 36px;
  }
  .section-top {
    justify-content: space-between;
  }
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 24px;
  }
  .section-top {
    &__link {
      height: 35px;
      padding: 5px 11px;
      font-size: 12px;
      i {
        width: 6px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .h2 {
    font-size: 20px;
  }
}