.main {
  padding: 10px 0 50px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 20px;
  }
  &__desc {
    position: relative;
    width: 55%;
    background: #F1E7EA;
    border-radius: 12px;
    padding: 50px 50px 60px 50px;
  }
  &__slider {
    position: relative;
    width: 45%;
    @media screen and (min-width: 768px) {
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        gap: 20px;
      }
      .swiper-slide {
        width: 100% !important;
      }
      .swiper-pagination {
        display: none;
      }
    }
    .swiper {
      height: 100%;
    }
    .swiper-wrapper {
      height: 100%;
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    max-width: 400px;
    font-size: 48px;
    line-height: 130%;
    font-weight: 700;
    span {
      color: #5856D2;
    }
  }
  &__items {
    position: relative;
    z-index: 2;
    max-width: 305px;
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 16px;
    &-icon {
      width: 30px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      span {
        display: block;
        font-weight: 600;
      }
    }
  }
  &__btn {
    position: relative;
    z-index: 2;
    height: 55px;
    display: inline-flex;
    padding: 5px 59px;
    margin-top: 16px;
    span {
      font-size: 16px;
    }
  }
  &__img {
    position: absolute;
    right: 16px;
    bottom: 0;
    width: 340px;
    img {
      width: 100%;
    }
  }
  &__slider2 {
    position: relative;
    margin-top: 20px;
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      min-width: 1385px;
      align-items: stretch;
    }
    .swiper-slide {
      width: auto !important;
      height: auto;
      flex-grow: 1;
    }
  }
}
.main-card {
  &__img {
    position: relative;
    height: 100%;
    border-radius: 12px;
    padding-bottom: 78%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__title {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 6px;
    background: #56BBD2;
    border-radius: 18px;
    padding: 9px 18px;
    margin: 15px;
    font-size: 11px;
    line-height: 130%;
    font-weight: 700;
    letter-spacing: 1.54px;
    color: #fff;
    i {
      width: 15px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
.main-card2 {
  position: relative;
  max-width: 270px;
  min-height: 113px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  border-radius: 12px;
  &--small {
    .main-card2__title {
      padding: 20px;
    }
    .main-card2__img {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
    }
  }
  &--colour {
    background: #F2EFF9;
  }
  &--colour2 {
    background: #EFF3F9;
  }
  &--colour3 {
    background: #F9F6EF;
  }
  &--colour4 {
    background: #EFF9F2;
  }
  &--colour5 {
    background: #F9EFEF;
  }
  &--colour6 {
    background: #EFF3F9;
  }
  &:last-child {
    margin-right: 0 !important;
  }
  &__title {
    position: relative;
    z-index: 2;
    order: -1;
    padding: 20px 5px 20px 20px;
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
  }
  &__img {
    max-width: 100px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
}
.events {
  padding: 50px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 30px;
  }
}
.event-card {
  position: relative;
  &:hover {
    &:after {
      opacity: 1;
    }
    .event-card__link {
      display: inline-flex;
    }
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50%;
    background: linear-gradient(0.00deg, rgb(63, 63, 63),rgba(63, 63, 63, 0) 100%);
    border-radius: 0 0 12px 12px;
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(88, 86, 210, 0.7);
    border-radius: 12px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &__img {
    position: relative;
    display: block;
    border-radius: 12px;
    padding-bottom: 62%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    padding: 20px 25px;
  }
  &__title {
    font-size: 24px;
    line-height: 130%;
    font-weight: 500;
    color: #fff;
  }
  &__link {
    display: none;
    align-items: center;
    gap: 12px;
    margin-top: 8px;
    &:hover {
      span {
        text-decoration-color: #fff;
      }
    }
    span {
      font-size: 14px;
      line-height: 130%;
      font-weight: 600;
      text-decoration: underline;
      text-decoration-color: transparent;
      color: #fff;
      transition: all 0.3s;
    }
    i {
      width: 14px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
.works {
  padding: 50px 0 100px;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  @media screen and (min-width: 768px) {
    .swiper-wrapper {
