      gap: 20px;
    }
    .swiper-slide {
      width: 100% !important;
    }
    .swiper-pagination {
      display: none;
    }
  }
  .swiper-wrapper {
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
  }
}
.work-card {
