  }
  &__img {
    width: 140px;
    height: 140px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  &__title {
    font-size: 20px;
    line-height: 130%;
    font-weight: 700;
    margin-bottom: auto;
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 20px;
    &:hover {
      span {
