      font-size: 14px;
      line-height: 130%;
      font-weight: 500;
      text-decoration: underline;
      color: #5856D2;
    }
    i {
      width: 14px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
  }
}
.connection {
  background: #EFF2F9;
  &__inner {
